import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

export default class Search extends React.Component {
  state = {
    search: "Hacker",
    type: "all"
  };

  handleKey = (e) => {
    if (e.key === "Enter") {
      this.props.searchMovies(this.state.search, this.state.type);
    }
  };

  handleFilter = (e) => {
    this.setState(() => ({type: e.target.dataset.type}), () => {
      this.props.searchMovies(this.state.search, this.state.type)
    })
  }

  render() {
    return (
      <div>
        <div class="input-field">
          <input
            placeholder="Search"
            type="text"
            className="validate"
            value={this.state.search}
            onChange={(e) => this.setState({ search: e.target.value })}
            onKeyDown={this.handleKey}
          />
          <button
            className="btn"
            onClick={() => this.props.searchMovies(this.state.search, this.state.type)}
          >
            <FontAwesomeIcon icon={faSearch} />
            Search
          </button>
        </div>
        <div className="box">
          <label>
            <input onChange={this.handleFilter} className="with-gap" name="group3" type="radio" data-type="all" checked={this.state.type === "all"} />
            <span>All</span>
          </label>
          <label>
            <input onChange={this.handleFilter} className="with-gap" name="group3" type="radio" data-type="movie" checked={this.state.type === "movie"} />
            <span>Movies only</span>
          </label>
          <label>
            <input onChange={this.handleFilter} className="with-gap" name="group3" type="radio" data-type="series" checked={this.state.type === "series"} />
            <span>Series only</span>
          </label>
        </div>
      </div>
    );
  }
}
